<template>
    <div class="header-wrapper" :class="{'fixed':fixed == 'fixed'}">
        <slot name="top">
	    <span class="back-icon" @click="navLeftClick"></span>
	</slot>
	<slot>
	    <span class="title">{{title}}</span>
	</slot>
	<slot name="bottom">
	    <span></span>
	</slot>
    </div>
</template>
<script type="text/javascript">



export default {
    name : "TopNav",
    props : {
	title : {type:String,default:""},
	fixed : {type:String,default:""}
    },
    data : function(){
        return{

        }
    },
    mounted(){
    },
    computed : {
	
    },
    methods : {
        navLeftClick(){
	    this.$router.go(-1);
	    this.$emit("clickLeft",{});
	},
	navRightClick(){
	    this.$emit("clickRight",{});
	},
    }
}
</script>
<style  rel='stylesheet/scss' lang='scss' scoped>
@import '../../assets/css/mixin.scss';
    .header-wrapper{
	width:100%;
	display:flex;
	justify-content:space-between;
	align-items:center;
	height:toRem(44px);
        background:#fff;
	box-sizing:border-box;
	padding:0px toRem(10px);
	box-shadow: 0px 0px toRem(4px) toRem(1px) #f5f5f5;
	&.fixed{
	    position:fixed;
	    z-index:10;
	    top:0;
	    left:0;
	}
	.back-icon{
	    display:inline-block;
	    width:toRem(24px);
	    height:toRem(24px);
	    @include bg-img("../../images/return");
	    &:active{
		@include bg-img("../../images/return_pr");
	    }
	}
	.title{
	    font-size:toRem(15px);
	    color:#333;
	}
    }

</style>



